import Vue from 'vue'
import Vuex from 'vuex'



Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    Ly_search:"",
    Ly_search_name:"",
    show:"",
    api_link:"https://api.lyrics.ovh/v1/",
    art_id_link:"https://www.theaudiodb.com/api/v1/json/1/search.php?s=",
    id:"",
    thumb:'',
    web:'',
    face:'',
    twit:'',
    video:'',
    social:true,
    youtube:"",
    songsname:"",
    mvyout:"",
    thumb_check:false,
    web_check:false,
    twit_check:false,
    face_check:false,
    show_check:false,
    yout_check:false,
    check_1:true
  },
  mutations: {

    async get_lyrics(){
      var x =this.state.Ly_search_name.replace('/ /g','%20');
      var z =this.state.Ly_search.replace('/ /g','%20');
      var link = this.state.api_link+x+"/"+z;
      const res = await fetch(link);
      const data= await res.json();
      this.state.show = data.lyrics;
    },
    async get_musicv(){
      var x =this.state.Ly_search_name.replace(' ','%20');
      var mv_id_link = this.state.art_id_link+x;
      var vid = "https://theaudiodb.com/api/v1/json/1/mvid.php?i="
      const resid = await fetch(mv_id_link);
      const datax = await resid.json();
      const finres = datax.artists;
      var z = finres[0].idArtist;
      var thumbnail = finres[0].strArtistThumb;
      var website = finres[0].strWebsite;
      var facebook = finres[0].strFacebook;
      var twitter = finres[0].strTwitter;
      this.state.id = z;
      var ht="https://"
      this.state.thumb =thumbnail;
      this.state.web = ht+website;
      this.state.face =ht+ facebook;
      this.state.twit = ht+twitter;
      this.state.video = vid +z;
      var xyz = vid +z;
      var resvid = await fetch(xyz);
      var dataxz = await resvid.json();
      var finresvid = dataxz.mvids;
      var arr_length = finresvid.length;
      for (var i=0 ; i<arr_length;i+=1){
        var xzy=finresvid[i].strTrack;
        this.state.songsname = xzy;
        if(xzy == this.state.Ly_search){ 
          var o =finresvid[i].strMusicVid;
          var refine = o.replace('watch?v=','embed/')
          this.state.mvyout=refine;
          console.log(i,o)
          break
        }

      }
   
    },
    Check(){
      console.log(this.state.yout_check.length)
      if(this.state.Ly_search_name.length > 0 &&this.state.Ly_search.length > 0 ){
        this.state.check_1 = !this.state.check_1;
        this.state.yout_check = !this.state.yout_check;
      }
    }
  },
  actions: {
  },
  modules: {
  }
})